// Mixins.

 $baseFontSize : 16;
@function rem($pixels, $context: $baseFontSize) {
    @if (unitless($pixels)) {
        $pixels: $pixels * 1px;
    }

    @if (unitless($context)) {
        $context: $context * 1px;
    }

    @return $pixels / $context * 1rem;
}
@mixin btn {
    display: inline-block;
    line-height: 1;


    color: #ffffff;
    font-size: 11px;
    font-weight: 600;
    letter-spacing: 1px;




    padding: rem(16) rem(32);
    text-align: center;
    transition: 0.2s background, 0.2s color, 0.2s border;
    position: relative;

    &:hover {

    }
}
