
.checkbox {
  font-family: $openSans;
  color: #6d6862;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.08;
  a {
    color: #7cb355;
    margin-left: 4px;
  }
  input {
    display: none;
  }
  & > span {
    padding-left: rem(22);
    position: relative;
    display: inline-block;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0px;
      transform: translateY(-50%);
      display: inline-block;
      border: 1px solid #a6a6a6;
      background-color: #ffffff;
      width: rem(17);
      height: rem(17);
      transition: 0.3s;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      display: inline-block;


      background-color:  #ececec;
      width: rem(17);
      height: rem(17);
      opacity: 0;
      transition: 0.3s;

    }
  }
  input:checked + span {
    &:before {
      content: '';
    }
    &:after {
      content: '';
      background-color:  #7cb355;
      transform: translateY(-50%) scale(0.6);
      opacity: 1;
    }
  }
}


.selectric {
  border: none !important;
  border-bottom: rem(2) solid  #e2e2e2 !important;
  border-radius: 0;
  background: transparent;
  position: relative;
  overflow: hidden;
}

.selectric .label {
  line-height: 1;
  height: auto;
  padding: 10px 32px;
  margin-left: 0;
  padding-right: 0;
  font-family: $openSans;
  color: #656565;
  font-size: 15px;
  font-weight: 400;
}
.selectric .button {
  background: transparent;
}
.selectric-items {
  border-radius: 3px;
  border: 1px solid #e8e8e8;
  background: #fff;
}

.selectric-items li:hover {
  background-color: rgba( #7cb355, 0.1);
}
.selectric-items li.selected {
  background-color:  #7cb355;
  color: #fff;
}


@include media-breakpoint-down(sm) {
  .b-form_figur {
    max-width: 100%;
  }
  .b-form__link {
    margin-right: 0;
  }
}
@include media-breakpoint-down(xs) {
  .b-form__bottom {
    flex-direction: column;
  }
  .b-form_figur {
    border-radius: 20px;
    padding: 20px;
  }
  .b-form__link {
    margin-top: rem(24);
    margin-left: 0;
  }

}
