.b-stat {

}
.b-stat__row {
  display: flex;
  flex-wrap: wrap;

}
.b-stat__left-col {
  flex: 0 0 60%;
  max-width: 60%;
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center;
  min-height: 100vh;
}
.b-stat__right-col {
  flex: 0 0 40%;
  max-width: 40%;
  background-size: cover;
  background-position: center;
  min-height: 100vh;
}
.b-stat__info {
  background-color: #193348;
  padding: 60px;
}
.b-stat__info_dark {
  background-color: #2f3337;
}

.b-stat__items {
  display: flex;
  flex-wrap: wrap;
}
.b-stat__item {
 flex: 1 0 25%;
  margin-right: 30px;
}
.b-stat__num {
  color: #d6b37e;
  font-size: rem(110);
  font-weight: 100;
  line-height: 1.03;
  white-space: nowrap;
}
.b-stat__val {
  font-size: 15px;
  font-weight: 300;
  line-height: 1.13;
  color: #ffffff;
}


