.page-header {
  padding-top: rem(70);
  .row {
    align-items: center;
  }
}
.page-header__logo {

}
.page-header__img {

}
.page-header__right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.page-header__tel {
  display: block;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  line-height: 0.72;
}
.page-header__callme {
  @include btn;
  @include btn-white-transparent;
  margin-left: rem(35);
}
.page-header__gamburger {
  margin-left: rem(35);
  position: relative;
  z-index: 60;
}

